<template>
  <head-bar page-title=""></head-bar>
  <div class="section-block">
    <div class="section-list">
      <div class="cell" v-link="{path:'/'}">home</div>
      <a class="cell" href="javascript:;">asd</a>
      <div class="cell" @click="bb">waiting</div>
      <div class="cell" @click="aa('...','waiting')">waiting</div>
    </div>
  </div>
  <div class="section-block">
    <div class="section-list">
      <div class="cell" @click="aa('','warn')">alert-warn</div>
      <div class="cell" @click="aa('','ok')">alert-ok</div>
      <div class="cell" @click="aa()">alert</div>
      <div class="cell" @click="kk">dialog-confirm</div>
    </div>
  </div>
  <loader :loading-show="loading" :alert-show="alert.show" :alert-type="alert.type" :alert-txt="alert.txt" :dialog-content="dialog.txt" :dialog-show="dialog.show"></loader>
  <nav-bar></nav-bar>
</template>
<script>
export default {
  data () {
    let self = this
    return {
      loading: true,
      alert: {
        show: false,
        type: null,
        txt: '',
        hideFn: function (a) {
          let timer = a || 1000
          setTimeout(function () {
            self.alert.type = null
            self.alert.show = false
            self.alert.txt = ''
          }, timer)
        }
      },
      dialog: {
        show: false,
        txt: '...',
        callback: null
      }
    }
  },
  route: {
    data (transition) {
      let self = this
      self.loading = false
    }
  },
  components: {
    'navBar': require('../components/navBar.vue'),
    'headBar': require('../components/header.vue'),
    'loader': require('../components/loader.vue')
  },
  methods: {
    aa () {
      let self = this
      let agm = arguments
      self.alert.show = true
      self.alert.type = agm[1] || null
      self.alert.txt = agm[0] || ''
      if (agm[1] === 'waiting') {
        console.log('asdasd')
      } else {
        self.alert.hideFn()
      }
    },
    bb (val) {
      console.log(val)
      let self = this
      self.alert.hideFn(10)
    },
    kk () {
      let self = this
      self.dialog.show = true
      self.dialog.txt = ''
      self.dialog.callback = function () {
        self.alert.show = true
        self.alert.txt = '!'
        self.alert.type = 'ok'
        self.alert.hideFn()
      }
    }
  },
  events: {
    'oncancel': function () {
      this.dialog.show = false
    },
    'onconfirm': function () {
      this.dialog.show = false
      this.dialog.callback()
    },
    'quitConfirm': function (e) {
      this.dialog.show = false
    }
  }
}

</script>
<style lang="stylus">
@import '../assets/styl/base'
a.cell
  -webkit-tap-highlight-color rgba(0, 0, 0, .1)
  color #888
</style>



