<template>
    <div class="slider clearfix">
        <ul class="clearfix" :style="{width:scaleW+'px'}">
            <li v-for="item in itemsForDom" track-by="$index" :style="{
                width:scaleW+'px',
                transform:'translate3d('+$index*scaleW+'px, 0, 0)'
            }">
            	<a v-if="item.href" :href="item.href"><img :src="item.picUrl" alt=""></a>
                <a v-else><img :src="item.picUrl"></a>
            </li>
        </ul>
        <div class="pagination" v-if="pagination">
            <i v-for="item in itemsLength" :class="{on:(current === $index) || (itemsLength === 2 && current%itemsLength === $index)}">{{$index+1}}</i>
        </div>
    </div>
</template>

<script>
export default {
	/*
		items 
			- 1
            - [{picUrl:'xxx',picId:'xxx',href:'xxxx'},{}.......]
        pagination  
            -  true
            - props :pagination="false" 
        autoPlay    
            -  true
            - props :auto-play="false" 
        speed   
            -  3000
            - props :speed="5000" 
	*/
  props: {
    sync: {
      type: Boolean,
      default: false // 
    },
    items: {
      type: Array
    },
    pagination: {
      type: Boolean,
      default: true
    },
    speed: {
      type: Number,
      default: 3000 // 
    },
    autoPlay: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      scaleW: window.innerWidth, // 
      sliedrWrap: null, // dom
      itemsDom: null, // dom
      itemsDomLength: undefined, // 
      current: 0, // 
      prev: undefined, // 
      next: 1, // 
      startTime: undefined, // 
      startX: undefined, // 
      offsetX: 0, // 
      timer: null // 
    }
  },
  computed: {
    itemsForDom: function () {
        // 
        //  === 
      var tmp = []
      if (this.items.length === 2) {
        for (var i = 0; i < 4; i++) {
          tmp.push(this.items[i % 2])
        }
        return tmp
      } else {
        return this.items
      }
    },
    itemsLength: function () {
        // 
      return this.items.length
    }
  },
  ready () {
    if (this.sync) {
      this.init() // 
    } else {
      this.$watch('items', function () {
        this.init() // ajaxitems
      })
    }
  },
  methods: {
    init: function () {
        // dom
      if (this.itemsLength < 2) {
        this.autoPlay = this.pagination = false
      } else {
        this.itemsDomLength = this.itemsLength === 2 ? 4 : this.itemsLength
        this.prev = this.itemsDomLength - 1 // 
        this.sliedrWrap = this.$el.getElementsByTagName('ul')[0] // dom
        this.itemsDom = this.sliedrWrap.getElementsByTagName('li') // dom
        this.addListener() // 
        this.autoPlay ? this.play(true) : '' // 
      }
    },
    goIndex: function (orientation) {
    /*  
        orientation  '+1''-1''0'
            +1  
            -1  
            0   
    */
      var _current = this.current
      var _prev = this.prev
      var _next = this.next
      var _cidx = 0
      var _time = 300 // 
      if (typeof orientation === 'string') {
        _cidx = _current + orientation * 1
      }
      // cidx -1 
      //  === 
      if (_cidx === -1) {
        _current = this.itemsDomLength - 1
        _prev = _current - 1
        _next = 0
      } else {
        _current = _cidx % this.itemsDomLength
        _prev = _current === 0 ? this.itemsDomLength - 1 : (_cidx - 1) % this.itemsDomLength
        _next = (_cidx + 1) % this.itemsDomLength
      }
      // 
      this.current = _current
      this.prev = _prev
      this.next = _next
      //  
      this.setTrans(this.itemsDom[_current], _time, 0)
      switch (orientation) {
        case '+1':
          this.setTrans(this.itemsDom[_next], 0, this.scaleW)
          this.setTrans(this.itemsDom[_prev], _time, -this.scaleW)
          break
        case '-1':
          this.setTrans(this.itemsDom[_prev], 0, -this.scaleW)
          this.setTrans(this.itemsDom[_next], _time, this.scaleW)
          break
        default:
          this.setTrans(this.itemsDom[_prev], _time, -this.scaleW)
          this.setTrans(this.itemsDom[_next], _time, this.scaleW)
      }
    },
    startHandler: function (evt) {
        /*
            
        */
      this.autoPlay ? this.play(false) : '' // 
      this.startTime = new Date() * 1 // 
      this.startX = evt.touches[0].pageX // 
      this.offsetX = 0 // 
    },
    moveHandler: function (evt) {
        /*
            
        */
      evt.preventDefault() // chrome androidtouchmove
      this.offsetX = evt.targetTouches[0].pageX - this.startX // 
      // 
      this.setTrans(this.itemsDom[this.current], 0, this.offsetX)
      this.setTrans(this.itemsDom[this.prev], 0, -this.scaleW + this.offsetX)
      this.setTrans(this.itemsDom[this.next], 0, this.scaleW + this.offsetX)
    },
    endHandler: function (evt) {
        /*
            
        */
      this.autoPlay ? this.play(true) : '' // 

      var _boundary = this.scaleW / 6 // 
      var _endTime = new Date() * 1 // 

      // 300ms 
      if (_endTime - this.startTime > 300) {
        if (this.offsetX >= _boundary) {
          this.goIndex('-1')
        } else if (this.offsetX < 0 && this.offsetX < -_boundary) {
          this.goIndex('+1')
        } else {
          this.goIndex('0')
        }
      } else {
          // 
        if (this.offsetX > 50) {
          this.goIndex('-1')
        } else if (this.offsetX < -50) {
          this.goIndex('+1')
        } else {
          this.goIndex('0')
        }
      }
    },
    play: function (play) {
        /*
            
        */
      var self = this
      if (play) {
        clearInterval(self.timer)
        self.timer = setInterval(function () {
          self.goIndex('+1') // 
        }, self.speed)
      } else {
        clearInterval(self.timer)
      }
    },
    setTrans: function (obj, time, target) {
        /* 
            obj DOM
            time 
            target 
        */
      obj.style.WebkitTransitionDuration = time + 'ms'
      obj.style.WebkitTransform = 'translate3d(' + target + 'px, 0, 0)'
    },
    addListener: function () {
        // 
      this.sliedrWrap.addEventListener('touchstart', this.startHandler)
      this.sliedrWrap.addEventListener('touchmove', this.moveHandler)
      this.sliedrWrap.addEventListener('touchend', this.endHandler)
    }
  }
}
</script>

<style lang="stylus">
@import '../assets/styl/index'
border-radius()
  border-radius arguments

.slider{
    width: 100%;
    height: px2rem(350)
    position: relative;
    text-align: center;
    overflow: hidden;
    ul{
        width: 100%;
        height: 4rem;
        overflow: hidden;
        margin 0
        padding 0
        list-style none
    }
    li{
        position: absolute;
    }

    a{
        display: block;
    }
    img{
    	width: 100%;
    }
    .pagination{
        z-index: 2;
        line-height: .5;
        position: absolute;
        width: 100%;
        left:0;
        bottom:.8em;
        i{
            display: inline-block;
            width: 6px
            height:  6px
            overflow: hidden;
            border-radius 100%;
            background: #fff;
            opacity: .3;
            margin:0 4px;
            text-indent: -9999px;
            &.on{
                background-color: #000;
            }
        }
    }
}
</style>



